<template>
  <div class="ranking">
    <van-nav-bar
      title="排行榜"
      left-text="返回"
      left-arrow
      fixed
      @click-left="goPage"
      @click-right="goPage('Bookrack')"
    >
      <template #right>
        <svg
          t="1594949373600"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="15150"
          width="24"
          height="24"
        >
          <path
            d="M264.154 952.655H64.31V224.11h199.844v728.545h-0.001z m-34.2-691.413H98.816v67.142h131.14v-67.142z m96.516-196.7h198.267v888.045H326.47V64.542z m168.989 34.214H360.143v69.235h135.315V98.756z m459.399 794.012L764.845 954.68 539.126 261.973l190.01-61.912 225.722 692.707zM708.125 245.964L583.438 286.59l20.798 63.834 124.686-40.627-20.797-63.833z m0 0"
            p-id="15151"
            fill="#B0C4DE"
          />
        </svg>
      </template>
    </van-nav-bar>
    <div class="content">
      <van-tabs
        v-model="active"
        title-inactive-color="#999"
        color="lightsteelblue"
        title-active-color="steelblue"
        @click="toggleTab"
      >
        <van-tab :title="item.title" v-for="(item,index) in type" :key="index">
          <div class="ranking-content clearfix">
            <div class="left fl">
              <div
                v-for="(val,i) in typeData[item.enname]"
                :class="[ranking_index == i ? 'active' :'']"
                :key="i"
                @click="toggleItem(i)"
              >{{val.shortTitle}}</div>
            </div>
            <div class="right fl" ref="right">
              <div class="item clearfix" v-for="(v,j) in bookData" :key="j" @click="goBook(v._id)">
                <div class="img-box fl">
                  <img class="auto-img" v-lazy="v.cover" alt="图片未加载..." />
                </div>
                <div class="text-box fl">
                  <div class="title">{{j+1}}.{{v.title}}</div>
                  <div class="name-box clearfix">
                    <span class="fl name">{{v.author}}</span>
                    <span class="fr type">{{v.minorCate}}</span>
                  </div>
                  <div class="text-content">{{v.shortIntro}}</div>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      type: [
        {
          title: "男频",
          enname: "male"
        },
        {
          title: "女频",
          enname: "female"
        },
        {
          title: "漫画",
          enname: "picture"
        }
      ],
      typeData: {},
      index: 0,
      // 排名分类id
      ranking_id: "",
      // 排名书籍数据
      bookData: [],
      type_index: 0,
      ranking_index: 0
    };
  },
  created() {
    this.getType();
  },
  methods: {
    // 返回 | 跳转到书架
    goPage(name) {
      if (name == "Bookrack") {
        this.$router.push({ name: name });
      } else {
        this.$router.go(-1);
      }
    },
    // 获取排行类型 书籍
    getType() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "http://novel.kele8.cn/rank-category"
      })
        .then(result => {
          this.$toast.clear();
 
          if (result.status == 200) {
            this.typeData = result.data;
            let type = this.type[this.type_index].enname;
            this.ranking_id = this.typeData[type][this.ranking_index]._id;
            this.axios({
              method: "get",
              url: "http://novel.kele8.cn/rank/" + this.ranking_id
            })
              .then(result => {
                this.$toast.clear();
 
                if (result.status == 200) {
                  this.bookData = result.data.ranking.books;
                  // 图片解URL码
                  this.bookData.forEach(val => {
                    val.cover = unescape(val.cover).slice(7);
                  });
                }
              })
              .catch(err => {
                this.$toast.clear();
 
              });
          }
        })
        .catch(err => {
          this.$toast.clear();
 
        });
    },
    // 切换排名分类数据
    toggleTab(index) {
      // 改变分类索引
      if (this.type_index == index) return;
      this.type_index = index;
      this.ranking_index = 0;
      this.getType();
    },
    // 切换小排名数据
    toggleItem(index) {
      if (this.ranking_index == index) return;
      this.ranking_index = index;
      this.$refs.right[0].scrollTop = 0;
      this.getType();
    },
    // 带id跳转到书籍详情页 
    goBook(id) {
      this.$router.push({ name: "Book", query: { id: id } });
    }
  }
};
</script>

<style lang="less" scoped>
.ranking {
  position: fixed;
  width: 100%;
  height: 100%;
  .content {
    margin-top: 46px;
    height: calc(~"100% - 46px");
    width: 100%;
  }
  .van-tabs,
  /deep/ .van-tabs__content,
  /deep/ .van-tab__pane {
    width: 100%;
    height: 100%;
  }
  .ranking-content {
    background-color: #fff;
    width: 100%;
    height: 100%;
  }
  .left,
  .right {
    padding: 10px 0;
    margin-left: 10px;
    font-size: 16px;
    height: calc(~"100% - 65px");
  }
  .left {
    color: lightsteelblue;
    line-height: 30px;
    width: 79px;
    border-right: 1px solid lightsteelblue;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    text-align: center;
    div {
      transition: all 0.3s;
      height: 30px;
      box-shadow: -1px -1px 3px lightsteelblue;
      margin: 3px 0;
      &.active {
        transform: scale(1.1);
        background-color: lightsteelblue;
        color: #fff;
        border-bottom-right-radius: 5px;
      }
    }
  }
  .right {
    width: calc(~"100% - 105px");
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 5px;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: lightsteelblue;
    }
    .item {
      width: 100%;
      margin-bottom: 10px;
      height: 90px;
      padding-bottom: 10px;
      border-bottom: 1px solid #ddd;
      &:last-child {
        margin-bottom: 0;
      }
      .img-box {
        width: 65px;
        height: 90px;
        img {
          height: 100%;
        }
      }
      .text-box {
        margin-left: 5px;
        width: calc(~"100% - 70px");
        box-sizing: border-box;
        height: 100%;
        .title {
          font-size: 18px;
          color: steelblue;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }
        .name-box {
          height: 20px;
          line-height: 20px;
          width: 100%;
          margin: 5px 0;
          color: #666;
          .type {
            font-size: 14px;
          }
        }
        .text-content {
          width: 100%;
          line-height: 20px;
          height: 40px;
          overflow: hidden;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          color: #999;
          text-align: justify;
          font-size: 14px;
        }
      }
    }
  }
}
</style>